<template>
  <div class="wrap_account">
    <div class="account">
      <Form v-if="isNew" as="div" class="create_account" v-slot="{ handleSubmit }" :validation-schema="createAccountSchema">
        <h2 v-text="'create account'"></h2>
        <form @submit="handleSubmit($event, onCreateAccount)">
          <TextInput name="email" type="email" label="" placeholder="Email" spellcheck="false" success-message="  ." />
          <TextInput name="password" type="password" label="" placeholder="Password" />
          <TextInput name="passwordConfirmation" type="password" label=" " placeholder="Type password again" success-message="  ." />
          <TextInput name="nickname" type="text" label="" placeholder="Nickname" spellcheck="false" success-message="   ." />
          <button type="submit" class="btn_submit"> </button>
        </form>
      </Form>

      <Form v-else as="div" class="modify_account" v-slot="{ handleSubmit }" :validation-schema="updateAccountSchema">
        <h2 v-text="'modify account'"></h2>
        <form @submit="handleSubmit($event, onUpdateAccount)">
          <TextInput name="email" type="email" label="" :disabled="true" :placeholder="user?.email" spellcheck="false" success-message="   ." />
          <TextInput name="currentPassword" type="password" label=" " placeholder="Current Password" />
          <TextInput name="password" type="password" label=" " placeholder="New Password" />
          <TextInput name="passwordConfirmation" type="password" label="  " placeholder="Type password again" success-message="  ." />
          <TextInput name="nickname" type="text" label="" :placeholder="user?.nickname" spellcheck="false" success-message="   ." />
          <button type="submit" class="btn_submit"> </button>
        </form>
      </Form>

      <div class="wrap_btn_sign-out">
        <button v-if="!isNew" @click="onDeleteAccount" class="btn_sign-out"> </button>
      </div>
    </div>
    <Dialog ref="Dialog"></Dialog>
  </div>
</template>

<script>
import { ref, computed } from 'vue'
import { useRoute } from 'vue-router'
import { useStore } from 'vuex'
import { Form } from 'vee-validate'
import * as Yup from 'yup'
import TextInput from '../../components/TextInput.vue'
import Dialog from '../../components/Dialog.vue'

export default {
  name: 'account',
  components: {
    Form,
    TextInput,
    Dialog,
  },
  setup() {
    const Dialog = ref(null)
    const route = useRoute()
    const { state, dispatch } = useStore()

    const user = computed(() => state.auth.user)
    const isNew = computed(() => !route.meta.requiredAuth)

    const createAccountSchema = Yup.object().shape({
      email: Yup.string().required('  .').email(),
      password: Yup.string()
        .required()
        .matches(/^(?=.*[a-zA-Z])(?=.*\d)[a-zA-Z\d!@#$%^&*]{4,30}$/, '4~30   , ,  .'),
      passwordConfirmation: Yup.string().oneOf([Yup.ref('password')], '  .'),
      nickname: Yup.string()
        .required(' .')
        .matches(/^[-a-zA-Z\d\S]{2,15}$/, '    . (,   )'),
    })

    const updateAccountSchema = Yup.object().shape({
      email: Yup.string()
        .default(user.value?.email ?? '')
        .email(),
      currentPassword: Yup.string().required(),
      newPassword: Yup.string()
        .nullable(true)
        .matches(/^(?=.*[a-zA-Z])(?=.*\d)[a-zA-Z\d!@#$%^&*]{4,30}$/, '4~30   , ,  .'),
      passwordConfirmation: Yup.string().oneOf([Yup.ref('password')], '  .'),
      nickname: Yup.string()
        .default(user.value?.nickname ?? '')
        .matches(/^[-a-zA-Z\d\S]{2,15}$/, '    . (,   )'),
    })

    const onCreateAccount = async (values) => await dispatch('auth/createAccount', values)

    const onUpdateAccount = async (values) => await dispatch('auth/updateAccount', values)

    const onDeleteAccount = async () => {
      const ok = await Dialog.value.show({
        title: 'Delete Account',
        message: 'Are you sure you want to delete your account?',
        okButton: 'Delete',
      })
      if (ok) {
        await dispatch('auth/deleteAccount')
      }
    }

    return {
      Dialog,
      user,
      isNew,
      createAccountSchema,
      updateAccountSchema,
      onCreateAccount,
      onUpdateAccount,
      onDeleteAccount,
    }
  },
}
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
.wrap_account {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  button {
    font-weight: 500;
    font-size: 1.3rem;
  }

  .account {
    border: 1px solid #ddd;
    padding: 6.4rem 3.2rem;
    border-radius: 3.2rem;

    h2 {
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 2.4rem;
      color: var(--primary-dark);
      font-weight: 400;
      margin: 0 0 calc(4.8rem - 3.2rem);
      text-transform: capitalize;
    }
  }
}

form {
  width: 30.4rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.btn_submit {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 4rem;
  color: #fff;
  background-color: var(--primary-dark);
  border-radius: 3.2rem;
  margin: 4.8rem 0 0;
}

.wrap_btn_sign-out {
  margin: 4.8rem 0 0;
  display: flex;
  align-items: center;
  justify-content: center;
  .btn_sign-out {
    color: #aaa;
    text-decoration: underline;
  }
}
</style>
